/*----------------- Portfolio.tsx ------------------ */

.content_box {
  position: relative;
  border: 1px solid black;
  border-radius: 5px;
  padding: 5px;
  margin: 0 auto;
  text-align: center;
  width: 800px;
  height: 450px;
}

.content_box_2 {
  position: relative;
  border: 1px solid black;
  border-radius: 5px;
  padding: 5px;
  margin: 0 auto;
  text-align: center;
  width: 600px;
}

.content_box_3 {
  position: relative;
  border: 1px solid black;
  border-radius: 5px;
  padding: 5px;
  margin: 0 auto;
  text-align: center;
  width: 100%;
}

.next_icon {
  position: absolute;
  top: 35%;
  right: -70px;
  transform: scale(1.0, 1.0);
  transition: 0.4s;
}

.next_icon:hover {
  transform: scale(1.2, 1.2);
  transition: 0.4s;
  cursor: pointer;
}

.back_icon {
  position: absolute;
  top: 35%;
  left: -70px;
  transform: scale(1.0, 1.0);
  transition: 0.4s;
}

.back_icon:hover {
  transform: scale(1.2, 1.2);
  transition: 0.4s;
  cursor: pointer;
}
/*----------------- Portfolio.tsx ------------------ */

/*----------------- Contact.tsx ------------------ */
.contact {
  width: 500px;
  margin: 0 auto;
  text-align: center;
  border: 1px solid black;
  padding: 20px 30px;
  border-radius: 5px;
}

.contact a {
  text-decoration: none;
  color: black;
  margin-left: 20px;
  cursor: pointer;
}

.mail {
  display: flex;
  justify-content: center;
  align-items: center;
}

.show_line_from_left {
  position: relative;
  transition: 0.5s;
}
.show_line_from_left::after {
  position: absolute;
  bottom: 0;
  left: 0;
  content: '';
  width: 0;
  height: 1px;
  background-color: #c22ecf	 !important;
  transition: 0.5s;
}
.show_line_from_left:hover::after {
  width: 100%;
}

.change_color_from_left {
  transition: 0.5s;
}
.change_color_from_left:hover {
  color: #c22ecf9f	 !important;
  transition: 0.5s;
}

.contact_icon {
  color: black;
  transform: scale(1.0, 1.0);
  transition: 0.4s;
}

.contact_icon:hover {
  color: #c22ecf9f;
  transform: scale(1.2, 1.2);
  transition: 0.4s;
}
/*----------------- Contact.tsx ------------------ */
